  transform: scaleX(1.22474) scaleY(0.70711) rotate3d(0, 0, 1, 45deg);
  position: relative; }

/*
Math.sqrt(2)
> 1.4142135623730951
1/1.4142135623730951
> 0.7071067811865475

1/(2*Math.tan(30*Math.PI/180)*Math.sin(45*Math.PI/180))
> 1.2247448713915892
1/1.2247448713915892
> 0.8164965809277259
*/
.triangle > .in > .in {
  transform-origin: left top;
  transform: rotate3d(0, 0, 1, -45deg) scale3d(0.8165, 1, 1) scale3d(1, 1.41421, 1) rotate3d(0, 0, 1, -30deg);
  position: absolute;
  left: 0;
  top: 0; }

.triangle:nth-child(2n) > .in > .in {
  transform: rotate3d(0, 0, 1, -45deg) scale3d(0.8165, 1, 1) scale3d(1, 1.41421, 1) rotate3d(0, 0, 1, -30deg) scale3d(-1, 1, 1); }

