  height: 692.8203230275509px;
  /* 2*(400.sin(60º)) */
  position: relative; }

.hexagon > .triangle {
  position: absolute;
  left: 400px;
  /* 800 / 2 */
  top: 346.41016151377545px;
  /* 692.8203230275509 / 2 */
  transform-origin: left top; }

.hexagon > .triangle:nth-child(1) {
  transform: rotate3d(0, 0, 1, 30deg); }

.hexagon > .triangle:nth-child(2) {
